body {
  font-family: 'Open Sans', sans-serif;
  color: #333333;
  background-color: #F0F0F0;
}

.hero {
  color: #FFFFFF;
  text-align: center;
  padding: 100px 20px;
}

.hero h1 {
  font-family: 'Montserrat', sans-serif;
  color: #007FFF;
}

.hero p {
  font-size: 18px;
  margin-bottom: 20px;
}

.button {
  background-color: #007FFF;
  color: #FFFFFF;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #0056b3;
}

